@import "../../../assets/sass/main.scss";

.feedback {

    background: $feedback-background;
     padding: 40px 0px 40px 40px;
    margin-right: 0px;
   
    

    .secondaryfeedback {
        display: flex;
         flex-direction: row;
         justify-content: space-around;
        flex-wrap: wrap;

        h5 {
            font-size: 19px;
            font-weight: 800;
        }
    }

    h4 {
        color: #000;
        font-weight: 600;
        font-size: 15px;
    }

    p {
        font-size: 13px;
        padding-top: 15px;
    }

    button {
        background-color: red;
        color: $white;
        margin-top: 40px;
    }

    .iso {
        // flex-direction: row;
        // display: flex;
        margin-bottom: 20px;
        margin-right: 20px;
        
        display: flex;
        justify-content: space-between;

        .icon {
            padding-right: 10px;
            height: 60px;
            width: 60px;
        }

        h6 {
            font-size: 15px;
        }
    }

}